@mixin colorSet($colorName,$color) {
  .#{$colorName} {
    color:$color !important;
  }
}
@include colorSet(black,$black)
@include colorSet(warm-grey,$warm-grey);
@include colorSet(medium-blue,$medium-blue);
@include colorSet(pinkish-grey,$pinkish-grey)
